.request-call {
  top: 124px;
  left: 50%;
  margin-left: -240px;
  width: 480px;
  border-radius: 8px;
}

.request-call__form {
  padding-top: 16px;
}

.request-call__form-container {
  margin: 0;
}

.request-call__item-wrapper {
  text-align: left;
}

.request-call__item-wrapper input[type="text"],
.request-call__item-wrapper input[type="tel"] {
  width: 360px;
  border: 2px solid #E3E3E3;
  border-radius: 80px;
  padding: 19px 40px 19px;
}

.request-call__item-wrapper input[type="text"] {
  margin-bottom: 8px;
}

.request-call__item-wrapper input[type="tel"] {
  margin-bottom: 22px;
  padding-bottom: 18px;
}

.request-call__item-wrapper input[type="text"]::placeholder,
.request-call__item-wrapper input[type="tel"]::placeholder {
  font-weight: 500;
  line-height: 20px;
  font-variant: small-caps;
  color: rgba($main-text-font, .5);
}

.request-call__item-wrapper input[type="text"]:focus,
.request-call__item-wrapper input[type="text"]:hover,
.request-call__item-wrapper input[type="tel"]:focus,
.request-call__item-wrapper input[type="tel"]:hover {
  outline: none;
  border: 2px solid $yellow;
}

.request-call__item-wrapper--button {
  text-align: center;
}

.request-call__item-wrapper button[type="submit"] {
  width: 264px;
  padding: 19px 40px 19px;
  text-align: center;
  border-radius: 80px;
  font-weight: 500;
  line-height: 20px;
  font-variant: small-caps;
  text-shadow: $shadow-text;
  margin-bottom: 16px;
  margin-left: -19px;
}

.request-call__item-wrapper--checkbox {
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
}

.request-call__item-wrapper--checkbox label {
  display: block;
  position: relative;
  cursor: pointer;
  width: 260px;
  font-size: 13px;
  line-height: 150%;
  color: rgba($main-text-font, .74);
  padding-left: 10px;
  margin: 0 auto;
}

.request-call__item-wrapper--checkbox label::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  border: 2px solid $yellow;
  border-radius: 2px;
  left: -14px;
  top: 0;
}

.request-call__item-wrapper--checkbox label:focus::before,
.request-call__item-wrapper--checkbox label:hover::before {
  opacity: 0.5;
}

.request-call__item-wrapper--checkbox input[type=checkbox] {
  position: absolute;
  left: 59px;
  top: 2px;
}

#agreement:checked+label::after {
  content: "";
  position: absolute;
  bottom: 18px;
  left: -19px;
  width: 27px;
  height: 23px;
  background-image: url("../img/checkbox-on.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}

@media (max-width: $mobile-width) {
  .request-call {
    top: 0;
    left: 0;
    margin-left: 0;
    width: 100%;
    height: 100%;
    border-radius: 0;
    overflow-y: auto;
  }
  .request-call__form {
    padding-top: 30px;
  }
  .request-call__item-wrapper {
    width: 288px;
    margin: 0 auto;
    text-align: center;
  }
  .request-call__item-wrapper input[type="text"],
  .request-call__item-wrapper input[type="tel"] {
    width: 288px;
  }
  .request-call__item-wrapper button[type="submit"] {
    margin-left: 1px;
  }
  .request-call__item-wrapper--checkbox label {
    width: 260px;
    text-align: left;
    padding-left: 20px;
  }
  .request-call__item-wrapper--checkbox label::before {
    left: -1px;
  }
  .request-call__item-wrapper--checkbox input[type=checkbox] {
    left: 20px;
    top: 10px;
  }
  #agreement:checked+label::after {
    left: -5px;
  }
}
